@charset 'utf-8';

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

ul,ol,li {
  list-style: none;
}

a {
  text-decoration: none;
}

body {
  background: #efefef;
}

table, th, td {
  border-collapse: collapse;
}

fieldset {
  border: none;
}

.h {
  position: absolute;
  top: -9999px;
  opacity: 0;
}